	module.exports = {"data":{"file":{"markdown":{"html":"<h1>Getting started</h1>\n<p>Install with:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\">yarn add redux<span class=\"token operator\">-</span>saga<span class=\"token operator\">-</span>firebase\n</code></pre>\n      </div>\n<p>Initialize a firebase app and instanciate redux-saga-firebase:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">import</span> firebase <span class=\"token keyword\">from</span> <span class=\"token string\">'firebase'</span>\n<span class=\"token keyword\">import</span> <span class=\"token string\">'@firebase/firestore'</span> <span class=\"token comment\">// ðŸ‘ˆ If you're using firestore</span>\n<span class=\"token keyword\">import</span> ReduxSagaFirebase <span class=\"token keyword\">from</span> <span class=\"token string\">'redux-saga-firebase'</span>\n\n<span class=\"token keyword\">const</span> myFirebaseApp <span class=\"token operator\">=</span> firebase<span class=\"token punctuation\">.</span><span class=\"token function\">initializeApp</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n  apiKey<span class=\"token punctuation\">:</span> <span class=\"token string\">\"qosjdqsdkqpdqldkqdkfojqjpfk\"</span><span class=\"token punctuation\">,</span>\n  authDomain<span class=\"token punctuation\">:</span> <span class=\"token string\">\"my-app.firebaseapp.com\"</span><span class=\"token punctuation\">,</span>\n  databaseURL<span class=\"token punctuation\">:</span> <span class=\"token string\">\"https://my-app.firebaseio.com\"</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token keyword\">const</span> reduxSagaFirebase <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">ReduxSagaFirebase</span><span class=\"token punctuation\">(</span>myFirebaseApp<span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<p>You can now use <code>reduxSagaFirebase</code> methods in your sagas:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">const</span> authProvider <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">firebase<span class=\"token punctuation\">.</span>auth<span class=\"token punctuation\">.</span>GoogleAuthProvider</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token keyword\">function</span><span class=\"token operator\">*</span> <span class=\"token function\">loginSaga</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">try</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">const</span> data <span class=\"token operator\">=</span> <span class=\"token keyword\">yield</span> <span class=\"token function\">call</span><span class=\"token punctuation\">(</span>reduxSagaFirebase<span class=\"token punctuation\">.</span>auth<span class=\"token punctuation\">.</span>signInWithPopup<span class=\"token punctuation\">,</span> authProvider<span class=\"token punctuation\">)</span>\n    <span class=\"token keyword\">yield</span> <span class=\"token function\">put</span><span class=\"token punctuation\">(</span><span class=\"token function\">loginSuccess</span><span class=\"token punctuation\">(</span>data<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n  <span class=\"token keyword\">catch</span><span class=\"token punctuation\">(</span>error<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">yield</span> <span class=\"token function\">put</span><span class=\"token punctuation\">(</span><span class=\"token function\">loginFailure</span><span class=\"token punctuation\">(</span>error<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> <span class=\"token keyword\">function</span><span class=\"token operator\">*</span> <span class=\"token function\">rootSaga</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">yield</span> <span class=\"token punctuation\">[</span>\n    <span class=\"token function\">takeEvery</span><span class=\"token punctuation\">(</span>types<span class=\"token punctuation\">.</span>LOGIN<span class=\"token punctuation\">.</span>REQUEST<span class=\"token punctuation\">,</span> loginSaga<span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">]</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>Make sure your client provides a implementation of <a href=\"https://developer.mozilla.org/en/docs/Web/API/Fetch_API\"><code>fetch</code></a>, either natively or via a polyfill (<a href=\"https://www.npmjs.com/package/whatwg-fetch\"><code>whatwg-fetch</code></a> is a pretty good one).</p>"}}},"pathContext":{}}

